/* @ngInject */
const TimesheetsService = ($http, appConstants, timesheetsServiceTransform, servicesTransform, $sessionStorage) => {
    const url = appConstants.url;
    const config = appConstants.config;
    const transformRequest = appConstants.transformRequest;
    appConstants.config.headers.token = $sessionStorage.user.token;

    const getData = (data) => {
        appConstants.config.headers.token = $sessionStorage.user.token;
        return $http({
            url: url + '?action=getData',
            method: 'POST',
            data,
            headers: appConstants.config.headers,
            transformRequest: servicesTransform.generate(timesheetsServiceTransform['getdataTransform']),
        });
    }

    const sumData = (data) => {
        appConstants.config.headers.token = $sessionStorage.user.token;
        return $http({
            url: url + '?action=getSums',
            method: 'POST',
            data,
            headers: appConstants.config.headers,
            transformRequest: servicesTransform.generate(timesheetsServiceTransform['getSumsDataTransform']),
        });
    }

    const insertData = (data) => {
        appConstants.config.headers.token = $sessionStorage.user.token;
        return $http({
            url: url + '?action=insertData',
            method: 'POST',
            data,
            headers: appConstants.config.headers,
            transformRequest: servicesTransform.generate(timesheetsServiceTransform['insertDataTransform']),
        });
    }

    const deleteData = (data) => {
        appConstants.config.headers.token = $sessionStorage.user.token;
        data.action = 'delete';
        return $http.post(url + '?action=deleteData', [data], config);
    }

    return {
        getData,
        insertData,
        deleteData,
        sumData,
    }
}

export default TimesheetsService;



