class DatatableController {
  /* @ngInject */
  constructor(timesheetsService, appConstants, $scope, $timeout) {

    this.name = 'datatable';
    this.appConstants = appConstants;
    this.$scope = $scope;
    this.$timeout = $timeout;
    this.timesheetsService = timesheetsService;
    this.currentPage = 1;
    this.showProgress = false;
  }

  $onChanges(changeObj) {
    if (changeObj.tableData && changeObj.tableData.currentValue) {
      this.getData(changeObj.tableData.currentValue);
    }
  }

  getData(data = this.tableData) {
    this.timesheetsService.getData(data).then((response) => {
      this.tableRecords = response.data[0];
      this.$timeout(() => {
        const options = {
          format: 'yyyy-mm-dd',
          autoClose: true,
        }
        let dateElements = '.datepicker.'+ this.tableData.table.name;
        let numberElements = '.numberpicker.'+ this.tableData.table.name;
        this.dateElements = document.querySelectorAll(dateElements);
        this.numberElements = document.querySelectorAll(numberElements);
        M.Datepicker.init(this.dateElements, options);
      });
      this.pagination();
    },(reason) => {
      let errorMsg = reason.data && reason.data.reason ? reason.data.reason : 'Unexpected Error Occurred';
      M.toast({html: errorMsg});
    });
  };

  insertData() {
    const data = this.transformData();
    !_.isNull(data) ? this.insertRecordInDb(data) : this.alertFillDetails();
  };

  insertRecordInDb(data) {
    this.showProgress = true;
    this.timesheetsService.insertData(data).then((response) => {
      this.tableRecords = response.data[0];
      this.pagination();
    },(reason) => {
      let errorMsg = reason.data && reason.data.reason ? reason.data.reason : 'Unexpected Error Occurred';
      M.toast({html: errorMsg});
    });
  }

  alertFillDetails() {
    M.Modal.init(document.querySelectorAll('.alertError'));
    M.Modal.getInstance(document.querySelectorAll('.alertError')[0]).open();
  }

  transformData() {
    let values = '';
    let comma = ',';
    let quot = "'";
    let isEmpty = false;
    let year = new Date().getFullYear();
    _.forEach(this.dateElements, (ele) => {
      let str = M.Datepicker.getInstance(ele).toString();
      isEmpty = _.isEmpty(str);
      values = values + quot + str + quot + comma;
    });
    _.forEach(this.numberElements, (ele) => {
      let str = ele.value;
      isEmpty = _.isEmpty(str);
      values = values + quot + str + quot + comma;
    });
    values = values + quot + year + quot;
    if (!isEmpty) {
      return {
        table: {
          name: this.tableData.table.name,
          values,
          column_name: this.tableData.table.column_name,
          limit : this.appConstants.limit,
          offset: 0,
        }
      }
    } else {
      return null;
    }
  }

  pagination() {
    this.showProgress = false;
    this.pagesCount = _.ceil(this.tableRecords.count / this.appConstants.limit);
    this.pages = _.range(1, this.pagesCount + 1);
  }

  getPage(page) {
    this.showProgress = true;
    this.currentPage = page;
    this.onGetPage({
      page: {
        page,
        name: this.tableData.table.name,
      }
    });
  }

  previousPage() {
    this.getPage(this.currentPage = _.indexOf(this.pages, this.currentPage) > 0 ? _.indexOf(this.pages, this.currentPage) : 1);
  }

  nextPage() {
    this.getPage(this.currentPage = _.indexOf(this.pages, this.currentPage) < _.last(this.pages) - 1 ?
      _.indexOf(this.pages, this.currentPage) + 2 : _.last(this.pages));
  }
}

export default DatatableController;



