/* @ngInject */
const servicesTransform = ($http) => {
    const generate = (transform) => {
        let defaults = $http.defaults.transformRequest;
        defaults = !_.isArray(defaults) ? [defaults] : defaults;
        transform = !_.isArray(transform) ? [transform] : transform;

        return transform.concat(defaults);
    }

    return {
        generate
    }
}

export default servicesTransform;


