import angular from 'angular';
import uiRouter from 'angular-ui-router';
import TimesheetsService from './timesheetsservice.service';
import TimeSheetsServiceTransform from './timesheetsservice.transform';

let timesheetsservicesModule = angular.module('timesheetsservices', [
  uiRouter
])

.service('timesheetsService', TimesheetsService)
.service('timesheetsServiceTransform', TimeSheetsServiceTransform)

.name;

export default timesheetsservicesModule;



