class LoginController {
  /* @ngInject */
  constructor($state, appConstants, servicesTransform, $http, usersService, $sessionStorage) {
    this.appConstants = appConstants;
    this.$state = $state;
    this.$http = $http;
    this.usersService = usersService;
    this.$sessionStorage = $sessionStorage;
  }
  login() {
    if (this.userName && this.password) {
      let data = [{
        action: "loginUser",
        table: {
          name: "user",
          column_name: "user_username",
          userName: this.userName.trim(),
          password: btoa(btoa(this.password)),
        }
      }];
      this.usersService.loginUser(data)
        .then((response) => {
          const reset = () => {
            this.$sessionStorage.$reset();
            this.$state.go('login');
          }
          const debounce = _.debounce(reset, this.appConstants.resetTime);
          if (response.data.data === 'Sucess') {
            this.$sessionStorage.$default({
              user: response.data
            });
            this.$state.go('csva');
          }
          debounce();
        },(reason) => {
          let errorMsg = reason.data && reason.data.reason ? reason.data.reason : 'Unexpected Error Occurred';
          M.toast({html: errorMsg});
        });
    }
  }
}

export default LoginController;



