/* @ngInject */
const TimeSheetsServiceTransform = ($http, appConstants) => {

    const reTransformData = (data) => {
        const tableRegEx = appConstants[data.table.name].tableRegEx;
        data.table.values = tableRegEx[0] + data.table.values + tableRegEx[1];
        return data;
    }

    const applyActions = (data, action) => {
        data = !_.isArray(data) ? [data] : data;
        return _.map(data, (value) => {
            return {
                table: value.table,
                action
            };
        });
    }

    const getdataTransform = (data) => {
        data = applyActions(data, appConstants.actions.selectAction);
        return data;
    }

    const getSumsDataTransform = (data) => {
        data = applyActions(data, appConstants.actions.selectSumAction);
        return data;
    }

    const insertDataTransform = (data) => {
        data = reTransformData(data);
        data = applyActions(data, appConstants.actions.insertAction);
        return data;
    }

    const deleteDataTransform = (data) => {
        data = applyActions(data, appConstants.actions.deleteAction);
        return data;
    }

    return {
        getdataTransform,
        getSumsDataTransform,
        insertDataTransform,
        deleteDataTransform,
    }
}

export default TimeSheetsServiceTransform;


