/* @ngInject */
const UsersService = ($http, appConstants, servicesTransform) => {
  const url = appConstants.url;
  const config = appConstants.config;

  const loginUser = (data) => {
    return $http({
      url: url + '?action=userLogin',
      method: 'POST',
      data,
      headers: appConstants.config.headers,
      transformRequest: servicesTransform.generate([]),
    });
  }

  return {
    loginUser
  }

}

export default UsersService;



