import angular from 'angular';
import uiRouter from 'angular-ui-router';
import csvaComponent from './csva.component';
// import _ from 'lodash';

let csvaModule = angular.module('csva', [
  uiRouter
])

  .config(($stateProvider) => {
    "ngInject";
    $stateProvider
      .state('csva', {
        url: '/csva',
        component: 'csva',
        onEnter: ($state, $rootScope, $localStorage, $sessionStorage, appConstants) => {
          if ($sessionStorage.user && $sessionStorage.user.token) {
            const reset = () => {
              $sessionStorage.$reset();
              $state.go('login');
            }
            const debounce = _.debounce(reset, appConstants.resetTime);
            debounce();
          } else {
            $state.go('login');
          }
        },
        onExit: () => {
          // alert('exit');
        },
      });
  })

  .component('csva', csvaComponent)

  .name;

export default csvaModule;



