import datatableComponent from "../datatable/datatable.component";

class TimesheetsController {
  /* @ngInject */
  constructor(appConstants, timesheetsService) {
    this.name = 'timesheets';
    this.appConstants = appConstants;
    this.timesheetsService = timesheetsService;
    this.hours = this.appConstants.hours;
    this.payments = this.appConstants.payments;
  }

  $onInit() {
    this.getSums();
  }

  onGetPage(data) {
    let column_name = data.name + '_id';
    let offset = (data.page - 1) * this.appConstants.limit;
    this[data.name].tableGetData = {
      table: {
        name: data.name,
        column_name,
        limit: this.appConstants.limit,
        offset,
      }
    }
  }

  getSums() {
    let data = []; let name = [];
    _.forEach(this.appConstants.sums, (value) => {
      data.push({
        table: {
          name: value.name,
          column_name: value.column,
          limit: this.appConstants.completeRows,
          offset: 0,
        }
      });
      name.push(value.column + '_' + 'sum');
    });
    this.timesheetsService.sumData(data).then((response) => {
      _.forEach(response.data, (value, key) => {
        this[name[key]] = parseInt(value.sum);
      },(reason) => {
        let errorMsg = reason.data && reason.data.reason ? reason.data.reason : 'Unexpected Error Occurred';
        M.toast({html: errorMsg});
      });
    });
  }

  downloadcsv = () => {
    let data = [];
    _.forEach(this.appConstants.sums, (table) => {
      data.push({
        table: {
          name: table.name,
          column_name: table.name + '_id',
          limit: this.appConstants.completeRows,
          offset: 0,
        },
      })
    });
    this.timesheetsService.getData(data).then((response) => {
      this.csvaData = response.data;
      this.workhours = this.csvaData[0].data;
      this.paymentsData = this.csvaData[1].data;
      this.miscellaneous = this.csvaData[2].data;
      this
      let csv1data = `Period,Hours
    `;
      this.workhours.forEach(data => {
        csv1data = csv1data + `${data.hours_start_date} - ${data.hours_end_date}, ${data.hours_hours}
      `;
      });
      csv1data = csv1data + `

    Total Hours, ${this.hours_hours_sum}

    `;

      let csv2data = `
    
    Cheque Period, Cheque Amount, Cheque Date
    `;
      this.paymentsData.forEach(data => {
        csv2data = csv2data + `${data.payments_start_date} - ${data.payments_end_date}, ${data.payments_amount}, ${data.payments_date}
      `;
      });
      csv2data = csv2data + `
    
    Total Paid, ${this.payments_amount_sum}
    
    `;

    let csv3data = `
    
    Miscellaneous Charges, Miscellaneous Amount
    `;
      this.miscellaneous.forEach(data => {
        csv3data = csv3data + `${data.miscellaneous_description} , ${data.miscellaneous_amount}
      `;
      });
      csv3data = csv3data + `
    
    Total Miscellaneous Charges, ${this.miscellaneous_amount_sum}
    
    `;
      let csvdata = csv1data + csv2data + csv3data;


      let a = document.createElement('a');
      a.setAttribute('href', 'data:attachment/csv;charset=utf-8,' + encodeURI(csvdata));
      a.setAttribute('target', '_blank');
      a.setAttribute('download', 'timesheets.csv');
      a.id = "someid";
      document.body.appendChild(a);
      document.getElementById('someid').click();
      document.getElementById('someid').remove();

    },(reason) => {
      let errorMsg = reason.data && reason.data.reason ? reason.data.reason : 'Unexpected Error Occurred';
      M.toast({html: errorMsg});
    });
  }
}

export default TimesheetsController;



